<template>
  <div class="bg-dark">
    <div class="emp w-100">
      <b-row>
        <b-col lg="5">
          <div class="vstack gap-3 info">
            <b-col>
              <b-badge variant="success">{{ dName }}</b-badge>
            </b-col>
            <b-col>
              <b-badge :variant="gender">{{ eName}}</b-badge>
            </b-col>
            <b-col>
              <b-badge variant="warning">{{ eID }}</b-badge>
            </b-col>
          </div>
        </b-col>
        <b-col lg="2">
          <div class="vstack gap-3 pt-4">
           <b-input-group>
            <template #prepend>
              <b-input-group-text>User ID</b-input-group-text>
            </template>
              <b-form-input v-model="input" placeholder="請輸入員編" type="password"></b-form-input> 
            </b-input-group>
              <div  class="hstack gap-3">
                  <b-form-radio v-model="modeSelected" name="some-radios" value="0" class="text-white">confetti</b-form-radio>
                  <b-form-radio v-model="modeSelected" name="some-radios" value="1" class="text-white" >Firework </b-form-radio>
                  <b-form-checkbox v-show="modeSelected==1" v-model="switchChecked" switch >
                    <strong class="text-white">{{ switchChecked?'open':'close' }}</strong>
                  </b-form-checkbox>
              </div>       
          </div>
        </b-col>
        <b-col lg="5">
          <div class="vstack gap-3 info">
            <b-col>
              <b-badge variant="success">{{ dName }}</b-badge>
            </b-col>
            <b-col>
              <b-badge :variant="gender">{{ eName}}</b-badge>
            </b-col>
            <b-col>
              <b-badge variant="warning">{{ eID }}</b-badge>
            </b-col>
          </div>
        </b-col>
      </b-row>
    </div>
  <div class="fireworks"></div>
 </div>
</template>
<style scoped>
  .emp span{
    text-align: center;
    font-size: 80px; 
  }
  .info div{
    padding-top: 1rem!important;
    padding-bottom: 0.5rem!important;
  }
  .emp {
    position: absolute;
    top:20%;
  }
</style>
<script scoped>
import { Fireworks } from 'fireworks-js'
import {modes} from '@/assets/shuffle.js'
//import confetti from 'canvas-confetti'
export default{
    name:'employee',
    data(){
      return {
        eID:'',
        eName:'',
        dID:'',
        dName:'',
        dataArray:[],
        input:'',
        gender:'primary',
        fireworks:null,
        switchChecked:false,
        modeSelected:0,
        conffetiType:0,
      }
    },
    mounted(){
      const jsonData= require(`../data/employee.json`);
      this.dataArray = jsonData;
      const container = document.querySelector('.fireworks');
      const fireworks = new Fireworks(container, {
              rocketsPoint: {
                min: 50,
                max: 50
              },
              hue: {
                min: 0,
                max: 360
              },
              delay: {
                min: 0.015,
                max: 0.03
              },
              lineWidth: {
                explosion: {
                  min: 1,
                  max: 3
                },
                trace: {
                  min: 1,
                  max: 2
                }
              },
              lineStyle: 'round',
              speed: 0.5,
              acceleration: 1.05,
              friction: 0.95,
              gravity: 1.5,
              particles: 60,
              traceLength: 3,
              flickering: 50,
              opacity: 0.5,
              explosion: 10,
              intensity: 30,
              traceSpeed: 8,
              autoresize: true,
              brightness: { 
                min: 50, 
                max: 80
              },
              decay : {
                min: 0.015,
                max: 0.05
              },
              mouse: { 
                click: false, 
                move: false, 
                max: 1 
              },
              boundaries: { 
                x: 50, 
                y: 50, 
                width: 0, 
                height: 0
              },
              sound: {
                enable: true,
                files: [
                  'explosion0.mp3'
                ],
                volume: { min: 4, max: 8 },
              }
            });
      fireworks.updateSize({width:1900,height:1000});
      this.fireworks = fireworks;
      //modes.snow();
    },
    watch:{
      input(val,oldValue) {
        if(val?.length > 0) {
          this.dName = this.eID = this.eName = '' ;
          var emp = this.dataArray.find(function(item,index,array){
            return item.EID==val}); 
            if(emp!==undefined) {
              this.delay(emp);
              this.getDepartment(emp);

            }       
        }//if
      },
      switchChecked(val,oldValue) {
        if(val&&this.modeSelected==1) {
          this.fireworks.start();
        }
        else {
          this.fireworks.stop();
        }  
      },
      modeSelected(val,oldValue) {
        if(val==0) {
          this.switchChecked = false;
        }
      }
      
    },
    methods:{
      getName(val) {
        this.eName = val?.Name+" "+val?.JobTitle;
        this.gender = (val?.Gender=='F')? 'danger' : 'primary';

      },
      delay(val){
          setTimeout(() => {
            this.getName(val);//4秒后执行
            this.checkEID(val);
            this.input = '';
            this.specialEffect()
            const audio = new Audio(this.randomSoundEffect());
            audio.play();
          }, 2500);
      },
      getDepartment(val) {
        this.dName = val?.DName; 
      },
      checkEID(val){
        this.eID = val?.EID; 
      },
      specialEffect(){
        if(this.modeSelected==0) {
          var ran;
          do{
             ran = Math.floor((Math.random() * 7));
          }while(ran==this.conffetiType)
          switch(ran) {
            case 0:
              modes.cannon();
              break;
            case 1:
              modes.realistic();
              break;
            case 2:
              modes.fireworks();
              break;
            case 3:
              modes.stars();
              break;
            case 4:
              modes.continuous();
              break;
            case 5:
              modes.snow();
              break;
            case 6:
              modes.random();
              break;
            default:
              modes.fireworks();
          }
          this.conffetiType = ran;
        } else {
          this.switchChecked = true;
        }
      },
      randomSoundEffect(){
        var x = Math.floor(Math.random()*5); //0,1,2,3,4
        var mp3Url ; 
        switch(x) {
          case 0:
            mp3Url = 'https://cdn.freesound.org/previews/462/462362_9159316-lq.mp3';
            break;
          case 1:
            mp3Url ='https://cdn.freesound.org/previews/51/51746_616218-lq.mp3';
            break;
          case 2:
            mp3Url = 'https://cdn.freesound.org/previews/241/241132_4157918-lq.mp3';
            break;
          case 3:
            mp3Url = 'https://cdn.freesound.org/previews/267/267248_5052946-lq.mp3';
            break;
          case 4:
            mp3Url = 'https://cdn.freesound.org/previews/274/274510_3029356-lq.mp3';
            break;      
          default:
            mp3Url = 'https://cdn.freesound.org/previews/594/594784_12590321-lq.mp3';
        }
        
        return mp3Url; 
      }
    },
}
</script>
