<template>
  <div class="lottery">
    <div class="my-2">
    <!--<b-container class="my-2">-->
      <b-row v-for="r in 5" class="mx-1 py-4">
        <b-col v-for="c in 10">
          <!--<b-button :variant="isDraw(10*(r-1)+c)?'info':'light'" >-->
            <b-badge :variant="cssArr[10*(r-1)+c-1]?'warning':'light'" class="numbers" :class="cssArr[10*(r-1)+c-1]?'':'text-opacity-25 text-light'" pill @click="clickNum(10*(r-1)+c)">{{10*(r-1)+c}}</b-badge>
          <!--</b-button>-->
        </b-col>
      </b-row>
    <!--</b-container>-->
    <div class="float-end px-4"><b-button variant="danger" @click="clear">Clear</b-button></div>
    </div>
  </div>
  
</template>
<style scoped>
.numbers {
  font-size: 3rem;
}
</style>
<script scoped>
export default{
    name:'employee',
    data(){
      return {
        dataArray:[],
        cssArr:[],
      }
    },
    mounted(){
      const jsonData= require(`../data/lottery.json`);
      //this.dataArray = jsonData.number;
      const cssArr = new Array(50).fill(false);
      this.cssArr = cssArr;
      if(!this.$cookies.isKey("drawArr")){
        this.$cookies.set("drawArr", " "); 
      } else{
        this.dataArray=this.$cookies.get("drawArr")?.split(',');
        this.dataArray.forEach((item)=>{
          this.cssArr[item-1] = true;
        });
      }
    },
    methods:{
      isDraw(val){
        var fn = this.dataArray.find(element=>element==val);
        return (fn!==undefined);
      },
      clickNum(val){  
        this.cssArr[val-1] = !this.cssArr[val-1];
        if(this.cssArr[val-1]){
          //true add number
          this.dataArray.push(val);
        } else {
          //remove number
          const filterNumbers = this.dataArray.filter((el)=>el!=val);
          this.dataArray = filterNumbers;
        }
        console.log(this.dataArray);
        this.$cookies.set("drawArr", this.dataArray); 
      },
      clear(event){
        this.$cookies.remove("drawArr");
        this.dataArray = [];
        this.cssArr = new Array(50).fill(false);
      }

    }
}
</script>
