<template>
  <div id="app">
    <transition name="fade">
       <div v-if="showMessage" class="alert alert-warning welcome-message border border-warning bd-callout" role="alert">
        <p class="alert-heading font-weight-bold" :class="gender">
          {{ eName}}
        </p>
        <p>{{ dName }}  {{ eID }}</p>
      </div>
    </transition>
    <div class="bg-dark">
    <div class="emp w-100 ">
      <div class="container-fluid">
        <div class="row w-100 vh-75 d-flex align-items-center justify-content-center">
          <div class="col-4  justify-content-center my-2"
          v-for="(card, index) in cards"
          :key="index">
            <div class="card mb-3">
               <div class="close-icon" @click="delCard(index)"></div>
              <div class="card-body" :class="card.txtColor">
                <p class="card-title">{{card?.Name}}</p>
                <p class="card-text text-dark">{{card?.DName}}<br/>{{card?.EID}}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="vstack gap-3 pt-4 w-25">
       <b-input-group>
        <template #prepend>
          <b-input-group-text>User ID</b-input-group-text>
        </template>
          <b-form-input v-model="input" placeholder="請輸入員編" type="password" 
          @keydown.space="handleSpaceKey" ></b-form-input> 
        </b-input-group>
          <div  class="hstack gap-3">
              <b-form-radio v-model="modeSelected" name="some-radios" value="0" class="text-white">confetti</b-form-radio>
              <b-form-radio v-model="modeSelected" name="some-radios" value="1" class="text-white" >Firework </b-form-radio>
              <b-form-checkbox v-show="modeSelected==1" v-model="switchChecked" switch >
                <strong class="text-white">{{ switchChecked?'open':'close' }}</strong>
              </b-form-checkbox>
          </div>  
      </div>
    </div>
    <div class="fireworks"></div>
    </div>
  </div>
</template>

<script>
import { Fireworks } from 'fireworks-js'
import {modes} from '@/assets/shuffle.js'
export default {
  data() {
    return {
      showMessage: false, // 控制提示訊息的顯示
      eID:'',
      eName:'',
      dID:'',
      dName:'',
      dataArray:[],
      input:'',
      gender:'text-primary',
      fireworks:null,
      switchChecked:false,
      modeSelected:0,
      conffetiType:0,
      cards:[],
    };
  },
  mounted() {   
    
      const jsonData= require(`../data/employee.json`);
      this.dataArray = jsonData;
      const container = document.querySelector('.fireworks');
      const fireworks = new Fireworks(container, {
              rocketsPoint: {
                min: 50,
                max: 50
              },
              hue: {
                min: 0,
                max: 360
              },
              delay: {
                min: 0.015,
                max: 0.03
              },
              lineWidth: {
                explosion: {
                  min: 1,
                  max: 3
                },
                trace: {
                  min: 1,
                  max: 2
                }
              },
              lineStyle: 'round',
              speed: 0.5,
              acceleration: 1.05,
              friction: 0.95,
              gravity: 1.5,
              particles: 60,
              traceLength: 3,
              flickering: 50,
              opacity: 0.5,
              explosion: 10,
              intensity: 30,
              traceSpeed: 8,
              autoresize: true,
              brightness: { 
                min: 50, 
                max: 80
              },
              decay : {
                min: 0.015,
                max: 0.05
              },
              mouse: { 
                click: false, 
                move: false, 
                max: 1 
              },
              boundaries: { 
                x: 50, 
                y: 50, 
                width: 0, 
                height: 0
              },
              sound: {
                enable: true,
                files: [
                  'explosion0.mp3'
                ],
                volume: { min: 4, max: 8 },
              }
            });
      fireworks.updateSize({width:1900,height:1000});
      this.fireworks = fireworks;
      //modes.snow();

  },
   watch:{
      input(val,oldValue) {
        if(val?.length > 0) {
          this.dName = this.eID = this.eName = '' ;
          var emp = this.dataArray.find(function(item,index,array){
            return item.EID==val}); 
            if(emp!==undefined) {
              
              var emplInfo = this.getInfo(emp);
              this.delay(emplInfo);
              this.showMessage = true;
            }       
        }//if
      },
      switchChecked(val,oldValue) {
        if(val&&this.modeSelected==1) {
          this.fireworks.start();
        }
        else {
          this.fireworks.stop();
        }  
      },
      modeSelected(val,oldValue) {
        if(val==0) {
          this.switchChecked = false;
        }
      }
      
    },
     methods:{
      delay(val){
          setTimeout(() => {
            this.input = '';
            this.specialEffect()
            const audio = new Audio(this.randomSoundEffect());
            audio.play();
            // 掃條碼後開啟
            this.showMessage = false;
            this.cards.push(val);
          }, 2500);
      },
      getInfo(val) {
        this.eName = val?.Name+" "+val?.JobTitle;
        this.gender  = (val?.Gender=='F')? 'text-danger' : 'text-primary';
        val.txtColor = (val?.Gender=='F')? 'text-danger' : 'text-primary';
        this.dName = val?.DName; 
        this.eID = val?.EID;
        val.index = this.cards.length;
        return val;
      },
      specialEffect(){
        if(this.modeSelected==0) {
          var ran;
          do{
             ran = Math.floor((Math.random() * 7));
          }while(ran==this.conffetiType)
          switch(ran) {
            case 0:
              modes.cannon();
              break;
            case 1:
              modes.realistic();
              break;
            case 2:
              modes.fireworks();
              break;
            case 3:
              modes.stars();
              break;
            case 4:
              modes.continuous();
              break;
            case 5:
              modes.snow();
              break;
            case 6:
              modes.random();
              break;
            default:
              modes.fireworks();
          }
          this.conffetiType = ran;
        } else {
          this.switchChecked = true;
        }
      },
      randomSoundEffect(){
        var x = Math.floor(Math.random()*5); //0,1,2,3,4
        var mp3Url ; 
        switch(x) {
          case 0:
            mp3Url = 'https://cdn.freesound.org/previews/462/462362_9159316-lq.mp3';
            break;
          case 1:
            mp3Url ='https://cdn.freesound.org/previews/51/51746_616218-lq.mp3';
            break;
          case 2:
            mp3Url = 'https://cdn.freesound.org/previews/241/241132_4157918-lq.mp3';
            break;
          case 3:
            mp3Url = 'https://cdn.freesound.org/previews/267/267248_5052946-lq.mp3';
            break;
          case 4:
            mp3Url = 'https://cdn.freesound.org/previews/274/274510_3029356-lq.mp3';
            break;      
          default:
            mp3Url = 'https://cdn.freesound.org/previews/594/594784_12590321-lq.mp3';
        }
        
        return mp3Url; 
      },
      delCard(index) {
        this.cards.splice(index, 1);
      }, 
      handleSpaceKey(event) {
        // 攔截空白鍵，防止在輸入框中插入空格
        event.preventDefault();
        this.removeCard();
      },
      removeCard(){
        this.cards = [];  
      }
    },
    
};
</script>

<style>
/* 全頁提示訊息樣式 */
.welcome-message {
  position: fixed;
  top: 10%;
  /*left: 5%;*/
  width: 100%;
  /*background-color: rgba(255, 255, 255);
  color: white;*/
  text-align: center;
  padding: 20px;
  font-size: 8.5rem;
  z-index: 9999;
}

/* 淡入淡出動畫 */
.fade-enter-active, .fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
.emp span{
  text-align: center;
  font-size: 80px; 
}
.info div{
  padding-top: 1rem!important;
  padding-bottom: 0.5rem!important;
}
.emp {
  position: absolute;
  /*top:20%;*/
}
.emp .card {
  font-size:2.5rem;
  background-color: #F0FFF0;
}

.card .card-title {
  font-size: 5.5rem;
  font-weight: bold;
}

/* Close Icon 樣式 */
.close-icon {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 30px;
  height: 30px;
  cursor: pointer;
  border-radius: 50%; /* 初始為圓形背景 */
  background-color: #eee;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease;
}

/* Icon 線條樣式 */
.close-icon::before,
.close-icon::after {
  content: '';
  position: absolute;
  width: 20px;
  height: 2px;
  background-color: #333;
  transform-origin: center;
  transition: all 0.3s ease;
}

.close-icon::before {
  transform: rotate(45deg);
}

.close-icon::after {
  transform: rotate(-45deg);
}

/* Hover 狀態樣式 */
.close-icon:hover {
  background-color: red; /* 改變背景色 */
  transform: scale(1.2); /* 放大 */
}

.close-icon:hover::before,
.close-icon:hover::after {
  background-color: white; /* 改變線條顏色 */
  transform: rotate(0deg); /* 換成水平和垂直線條 */
}

</style>